@charset "UTF-8";
.vetskitchen-accordion {
  margin-bottom: 15px;
}
.vetskitchen-accordion__item {
  border: 1px solid #fff3eb;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.05);
}
.vetskitchen-accordion__item-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  min-height: 54px;
  font-family: "Hind", sans-serif;
  font-weight: 300;
  font-size: 18px;
  color: #000000;
  line-height: 1.2;
  background-color: #fff3eb;
  cursor: pointer;
}
.vetskitchen-accordion__item-header:after {
  content: "＋";
  margin-left: 20px;
  font-size: 22px;
  font-weight: 400;
  transition: 0.3s transform;
}
.vetskitchen-accordion__item-header[aria-expanded=true]:after {
  transform: rotate(-225deg) translateY(3px);
}
.vetskitchen-accordion__item-content {
  padding: 15px 25px;
  color: #000000;
}
.vetskitchen-accordion__item-content > *:last-child {
  margin-bottom: 0;
}
.vetskitchen-accordion__item + .vetskitchen-accordion__item {
  margin-top: 5px;
}